input[type="radio"] {
  display: none;
}
input[type="radio"] + span {
  display: inline-block;
  position: relative;
  top: -1px;
  width: 100px;
  height: 30px;
  margin: 5px;
  vertical-align: middle;
  background-color: #c6e194;
  border: 1px solid #ccc;
  cursor: pointer;
}
input[type="radio"]:checked + span {
  background: #7dba02 -19px top no-repeat;
}

input[type="radio"] + span {
  margin-right: 4px;
}
input[type="radio"] + span:hover {
 transform: translateY(-3px);
}

.reset-filter-btn{
  width: 50%;
  height: 30px;
  vertical-align: middle;
  background-color: #c6e194;
  cursor: pointer;
border:none;
outline:none;
border-radius:5px;
margin:5px 0px;
font-size:17px;
font-weight:600;
}
.reset-filter-btn:hover{
  background-color: #7dba02;
  transform: translateY(-3px);
}